.Container {
    display: flex;
    flex-flow: column;
    padding: 20px 40px;
    margin: 0 auto;
    max-width: 650px;
    background-color: rgba(253, 242, 242, 0.6);
    margin-top: 40px;
}

.Field {
    padding: 8px 0px;
    line-height: 22px;
}

.Field > div {
    display: flex;
    justify-content: space-between;
}

.Field > div > label {
    font-weight: 500;
}

.Field > div > div {
    flex-grow: 1;
    max-width: 320px;
    display: flex;
    justify-content: space-between;
}
.Field > div > div > input {
    flex-grow: 1;
    max-width: 320px;
    outline: none;
}
.Field > div > div > span {
    font-size: 12px;
    color: red;
    margin-left: 10px; 
}

.Intro {
    display: flex;
    justify-content: space-between;
    min-height: 80px;
}

.Intro > label {
    font-weight: 500;
}

.Intro > textarea {
    flex-grow: 1;
    max-width: 320px;
    outline: none;
}

.Required:after
{
    color: red;
    content: " *";
}

.Submit, .SubmitError {
    margin-top: 32px;
    display: flex;
    justify-content: flex-end;
}

.SubmitError {
    color: red;
    font-weight: 600;
    font-size: 20px;
}
.SubmitSuccess {
    color: green;
    font-weight: 600;
    font-size: 20px;
}

.Submit > button {
    cursor: pointer;
    padding: 6px 16px;
    font-size: 16px;
    min-width: 64px;
    box-sizing: border-box;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-weight: 500;
    line-height: 1.75;
    border-radius: 4px;
    letter-spacing: 0.02857em;
    text-transform: uppercase;
    outline: none;
}

.Submit > button:hover {
    background-color: #c0f7c0;
    border: 1px solid #c0f7c0;
    color: brown;
}